[data-alc-theme="dark"] alc-radio {
  --accent-color: var(--alc-color-action-40);
  --outline-color: var(--alc-color-action-30);

  --text-color: var(--alc-color-text-4);
  --text-color-error: var(--alc-color-error-45);
}

[data-alc-theme="light"] alc-radio {
  --accent-color: var(--alc-color-action-60);
  --outline-color: var(--alc-color-action-60);
  
  --text-color: var(--alc-color-text-4);
  --text-color-error: var(--alc-color-error-50);
}

alc-radio {
  @apply flex gap-2 items-start;
}

alc-radio input[type='radio'] {
  @apply mt-px;
  @apply focus:outline focus:outline-offset-2 focus:outline-2;

  /* Light */
  @apply accent-[--accent-color];
  @apply focus:outline-[--outline-color];
}

.alc-radio__label {
  @apply flex flex-col text-sm font-medium;
}

.alc-radio__text {
  @apply text-sm font-normal text-[--text-color];

  &--error {
    @apply text-[--text-color-error];
  }

}
